@media screen and (max-width: 767px), screen and (max-device-width: 767px) {
  body{
    .footer{
      &__nav{
        .container{
          .row{
            .col-xs-8,
            .col-xs-4{
              .footer__menu{
                ul{
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  li{
                    a{
                      font-size: .823em;
                      line-height: 1.25em;
                    }
                  }
                }
              }
            }
            .col-xs-12{
              ul{
                display: flex;
                flex-direction: column;
                align-items: center;
                li{
                  a{
                    font-size: .823em;
                    line-height: 1.25em;
                    text-align: center;
                  }
                }
              }
            }
          }
        }
      }
      &__info{
        .container{
          .row{
            .col-xs-12{
              .copyright,
              .payments{
                display: flex;
                flex-direction: column;
                align-items: center;
                p{
                  text-align: center;
                }
              }
            }
          }
        }
      }
    }
  }
}