@media screen and (max-width: 767px), screen and (max-device-width: 767px) {
  body .footer__nav .container .row .col-xs-8 .footer__menu ul,
  body .footer__nav .container .row .col-xs-4 .footer__menu ul {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  body .footer__nav .container .row .col-xs-8 .footer__menu ul li a,
  body .footer__nav .container .row .col-xs-4 .footer__menu ul li a {
    font-size: .823em;
    line-height: 1.25em;
  }
  body .footer__nav .container .row .col-xs-12 ul {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  body .footer__nav .container .row .col-xs-12 ul li a {
    font-size: .823em;
    line-height: 1.25em;
    text-align: center;
  }
  body .footer__info .container .row .col-xs-12 .copyright,
  body .footer__info .container .row .col-xs-12 .payments {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  body .footer__info .container .row .col-xs-12 .copyright p,
  body .footer__info .container .row .col-xs-12 .payments p {
    text-align: center;
  }
}
